<template>
  <section>
      <section class="open-wrap-content">
        <compontSearch @searchData="fetch(1)" :ruleForm="searchRuleForm" :ruleData="searchRuleData"></compontSearch>
        <el-table :data="userList" border @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55" :selectable="(row)=>{return row.checkState==constans['STATE_UNAUDITED']}"></el-table-column>
            <!--<el-table-column label="" type="index" width="80"></el-table-column>-->
          <el-table-column label="" prop="userName"></el-table-column>
          <el-table-column label="" prop="email"></el-table-column>
          <!--<el-table-column label="" prop="telNumber"></el-table-column>-->
          <el-table-column label="" prop="enterpriseName"></el-table-column>
          <!--<el-table-column label="" prop="regtime" :formatter="formatter"></el-table-column>-->
          <!--<el-table-column label="" prop="mem" show-overflow-tooltip></el-table-column>-->
          <!--<el-table-column label="" prop="state" :formatter="formatterUserState"></el-table-column>-->
          <el-table-column label="">
            <template scope="scope">
              <el-popover trigger="hover" placement="top" v-if="scope.row.checkState*1 == constans['STATE_NOPASS']">
                <p v-if="scope.row.checkMem">{{ scope.row.checkMem }}</p>
                <div slot="reference" class="name-wrapper">
                  <el-tag>{{ checkState[scope.row.checkState] }}</el-tag>
                </div>
              </el-popover>
              <p v-else>
                {{checkState[scope.row.checkState*1]}}
              </p>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template scope="scope">
              <p v-if="scope.row.state*1 == constans['STATE_OPEN']">
                {{state[scope.row.state*1]}}
              </p>
              <p v-else>
                {{state[constans['STATE_RESERVE']]}}
              </p>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template scope="scope">
              <a class="open-list-btn" size="small" @click="showPop(scope.$index)" v-if="scope.row.checkState==constans['STATE_UNAUDITED']"></a>
              <a class="open-list-btn" size="small" @click="modUserState(scope.$index, scope.row.id, !scope.row.state * 1)">{{userState[!scope.row.state*1]}}</a>
              <!--<a class="open-list-btn" @click="delUser(scope.$index, scope.row.id)"></a>-->
              <!--<a class="open-list-btn" @click="resetPwd(scope.row.id)"></a>-->
            </template>
          </el-table-column>
        </el-table>
        <el-button @click="showPopBatch()" :disabled="!Boolean(this.multipleSelection.length)"></el-button>
        <el-pagination @current-change="handleCurrentChange" layout="total, prev, pager, next" :total="total" :page-size="pageSize" :current-page="currentPage" class="open-page" v-if="total"></el-pagination>
      </section>

      <el-dialog title="" :visible.sync="visible">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="open-form">
          <template v-if="!batch">
            <el-form-item label="" class="open-form-input">
              <el-input v-model="ruleForm.enterpriseName" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item label="" class="open-form-input">
              <el-input v-model="ruleForm.idCard" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item label="" class="open-form-input">
              <el-input v-model="ruleForm.userRealName" :disabled="true"></el-input>
            </el-form-item>
          </template>
          <el-form-item label="" class="open-form-input" prop="checkState">
            <el-radio-group v-model="ruleForm.checkState">
              <el-radio v-for="(state, key) in checkState" class="radio" :label="key" v-if="key==constans['STATE_PASS'] || key==constans['STATE_NOPASS']">{{state}}</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="checkMem" class="open-form-input" v-if="ruleForm.checkState == constans['STATE_NOPASS']">
            <el-input v-model="ruleForm.checkMem" placeholder=""></el-input>
          </el-form-item>
          <el-form-item class="open-center">
            <el-button class="open-login-btn" type="primary" @click="submitForm('ruleForm')"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import compontLeft from '@/components/LEFT.vue'
  import compontSearch from '@/components/SEARCH.vue'
  import userService from '@/api/userService.js'
  import { formatDate } from '@/assets/js/lib.js'
  import md5 from 'js-md5'
  export default{
    name: 'api-supplier',
    data () {
      return {
        batch: false, // 
        constans: constans, // 
        userType: this.$route.meta.userType,
        targetName: this.$route.meta.index,
        navData: config.navLeft,
        currentPage: 1,
        pageSize: 10,
        userList: [],
        total: 0,
        checkState: config['userCheckState'],// 
        state: config.gwState, // 
        password: config.resetPwd, // 
        ruleForm: {
          id: '',
          checkState: '2',
          checkMem: ''/* ,
          link: '/login' */
        },
        rules: {
          checkState: [{
              required: true,
              trigger: 'blur',
              message: ''
            }],
          checkMem: [{
            required: true,
            trigger: 'blur',
            message: ''
          }]
        },
        visible: false,
        index: '',
        userState: config.gwState,
        searchRuleForm: {
          userName: '',
          email: '',
          checkState: '',
          state: ''
        }, // 
        searchRuleData: {
          userName: {
            name: ':',
            type: 'input',
            placeholder: ''
          },
          email: {
            name: ':',
            type: 'input',
            placeholder: ''
          },
          checkState: {
            name: ':',
            type: 'select',
            data: {'': '', ...config['userCheckState']},
            placeholder: ''
          },
          state: {
            name: '',
            type: 'select',
            data: {'': '', ...config['gwState']}
          }
        }, // 
        multipleSelection: []
      }
    },
    watch: {
      '$route': 'fetchData'
    },
    created () {
      this.fetchData()
    },
    methods: {
      //
      fetchData () {
        this.userType = this.$route.meta.userType
        this.targetName = this.$route.meta.index
        this.searchRuleForm =  {
          userName: '',
          email: '',
          checkState: '',
          state: ''
        }
        this.fetch()
      },
      /*formatterUserState (row) {
        if (row.checkState ==1) {
          return this.userState[row.state*1]
        } else {
          return '--'
        }
      },*/
      formatter (row, column) {
        return formatDate(row.regtime)
      },
      //
      fetch (currentPage = this.currentPage) {
        this.total = 0
        this.userList = []
        let userType = this.userType
        let pageSize = this.pageSize
        let userName = this.searchRuleForm.userName
        let email = this.searchRuleForm.email
        let checkState = this.searchRuleForm.checkState
        let state = this.searchRuleForm.checkState
        userService.getResponse('userList', {
          userType,
          currentPage,
          pageSize, userName, checkState, email, state}).then((response) => {
            this.userList = response.items
            this.total = response.totalNumber
            this.currentPage = currentPage
          })
          .catch(error => {
            this.$message.error(error)
          })
      },
      handleCurrentChange (pIndex=1) {
        this.currentPage = pIndex
        this.fetch()
      },
      // 
      delUser (index, id) {
        userService.getResponse('delUser', {id})
          .then((response) => {
            this.userList.splice(index, 1)
            this.total--
          })
          .catch(error => {
            this.$message.error(error)
          })
      },
      //
      showPop (nIndex) {
        this.visible = true
        this.index = nIndex
        this.batch = false
        this.ruleForm.id = this.userList[nIndex].id
        this.ruleForm.enterpriseName = this.userList[nIndex].enterpriseName
        this.ruleForm.idCard = this.userList[nIndex].idCard
        this.ruleForm.userRealName = this.userList[nIndex].userRealName
        this.ruleForm.checkState = '2'
        this.ruleForm.checkMem = ''
      },
      // 
      showPopBatch () {
        this.visible = true
        this.batch=true
        let id = []
        this.multipleSelection.map(item=>{
          id.push(item.id)
        })
        this.ruleForm.id = id.join(',')
        this.ruleForm.checkState = '2'
      },
      submitForm (ruleForm) {
        this.$refs[ruleForm].validate((valid) => {
          if (valid) {
            this.auditUser()
          } else {
            // this.$message.error('!!')
            return false
          }
        })
      },
      // batchReview
      auditUser () {
        let userIds = this.ruleForm.id
        let checkMem = this.ruleForm.checkMem
        let checkState = this.ruleForm.checkState
        userService.getResponse('auditUser' ,{userIds, checkMem, checkState})
          .then((response) => {
            this.$message.success('')
            this.visible = false
            /*this.userList[this.index].checkState = this.ruleForm.checkState
            this.userList[this.index].checkMem = this.ruleForm.checkMem
            this.userList[this.index].state = constans['STATE_OPEN']*/
            this.fetch()
          })
          .catch(error => {
            this.$message.error(error)
          })
      },
      //
      modUserState (index, userId, state) {
        userService.getResponse('modUserState', {userId, state})
          .then((response) => {
            this.userList[index].state = state
            this.$message.success(state ? '' : '');
          })
          .catch(error => {
            this.$message.error(error)
          })
      },
      //
      resetPwd (userId) {
        let newPassword = md5(this.password)
        console.log(this.password)
        userService.getResponse('resetPwd', {userId, newPassword})
          .then((response) => {
            this.$message.success(`${this.password}`)
          })
          .catch(error => {
            this.$message.error(error)
          })
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      }
    },
    components: { compontLeft, compontSearch }
  }
</script>



