import constans from './constans.js'
const config = {
  tyk: 'http://www.tyk/',
  validate: {
    userName: /[\w-*]+$/,
    passWord: /^[a-zA-Z0-9]+[\w.\-*@?!]+$/,
    email: /^[\w-]+@[\w-]+(\.[\w-]+)+$/,
    tel: /^((13[0-9])|(15[^4])|(18[0-9])|(17[0-8])|(147,145))\d{8}$/,
    apiChName: /[\u4e00-\u9fa5]+$/,
    apiEnName: /[a-zA-Z0-9]+$/,
    ip: /((2[0-4]\d|25[0-5]|[01]?\d\d?)\.){3}(2[0-4]\d|25[0-5]|[01]?\d\d?)/,
    paramName: /^[a-zA-Z]+\w\$$/,
    errname: /^\\w+[\w.-]+$/,
    appName: /[\u4e00-\u9fa50-9\w-*]+$/
  },
  expiredTime: 3600 * 24 * 1000, // 
  userInfo: { // 
    userName: null,
    mem: null,
    id: null
  },
  argTypes: [
    'int', 'string', 'data', 'float', 'double'
  ],
  httpmds: ['GET', 'POST'],
  code: {
    0: '',
    1: '',
    3: ''
  },
  gwRate: {
    maxRate: 100, // 
    quotaMax: 5000, // 
    quotaRenewalRate: {
      '3600000': '1', '84600000': '1'
    }
  },
  pageSize: 10000,
  resetPwd: '123456',
  userType: {
  },
  openState: {
  },
  requireds: {
  },
  navLeft: {
  },
  gwState: {
  },
  checkState: {
  },
  userCheckState: {}
}
config['userType'][constans['ADMIN_USER']] = ''
config['userType'][constans['SERVICE_USER']] = ''
config['userType'][constans['SUPPLY_USER']] = 'API'

config['openState'][constans['API_OPEN']] = ''
config['openState'][constans['API_RETAIN']] = ''

config['gwState'][constans['STATE_OPEN']] = ''
config['gwState'][constans['STATE_RESERVE']] = ''

config['checkState'][constans['STATE_UNCOMMITTED']] = ''
config['checkState'][constans['STATE_UNAUDITED']] = ''
config['checkState'][constans['STATE_PASS']] = ''
config['checkState'][constans['STATE_NOPASS']] = ''

config['userCheckState'][constans['STATE_UNCOMMITTED']] = ''
config['userCheckState'][constans['STATE_UNAUDITED']] = ''
config['userCheckState'][constans['STATE_PASS']] = ''
config['userCheckState'][constans['STATE_NOPASS']] = ''

config['requireds'][constans['REQUIRED']] = ''
config['requireds'][constans['NO_REQUIRED']] = ''

config['navLeft'] = {
  data: [{
    name: '',
    routerName: 'supplyMan'
  }, {
    name: '',
    routerName: 'developerMan'
  }, {
    name: '',
    routerName: 'openApplyMan'
  }, {
    name: '',
    routerName: 'diyApplyMan'
  }, {
    name: '',
    routerName: 'gwList'
  }]
}
export { config as default }



