import Vue from 'vue'
import Router from 'vue-router'
import constans from '@/assets/js/constans.js'
import header from '@/components/HEAD.vue' // 
import compontCurmbs from '@/components/CURMBS.vue'
import footer from '@/components/FOOTER.vue'
import login from '../views/login.vue' // 
import wrap from '@/components/WRAP.vue'

// import signUp from '../views/signUp.vue' // 
import gwList from '@/views/personCenter/gwList.vue'
import nodeList from '@/views/personCenter/nodeList.vue'
import supplyMan from '@/views/personCenter/supplyMan.vue' // 
// import groupMan from '@/views/personCenter/groupMan.vue' // 
// import apiMan from '@/views/personCenter/apiMan.vue' // api
import openApplyMan from '@/views/personCenter/openApplyMan.vue' // 
import openApplyDetail from '@/views/personCenter/openAppDetail.vue' // 
import apiDetail from '@/views/personCenter/apiDetail.vue' // api
import guideDetail from '@/views/personCenter/guideDetail.vue' // 
import diyApplyMan from '@/views/personCenter/diyApplyMan.vue' // 
import diyApplyDetail from '@/views/personCenter/diyAppDetail.vue' // 

Vue.use(Router)

let router = new Router({
  // base: '../',
  // mode: 'history',
  routes: [
    /* {
      path: '*',
      redirect: {
        name: 'gwList'
      }
    }, */
    {
      path: '/',
      redirect: {
        name: 'login'
      }
    }, {// 
      path: '/login',
      name: 'login',
      component: login,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/openCenter',
      redirect: {
        name: 'supplyMan'
      },
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: wrap,
        footer: footer
      },
      children: [
        { // -
          path: 'supplyMan',
          name: 'supplyMan',
          component: supplyMan,
          meta: {
            title: '',
            userType: constans['SUPPLY_USER'],
            index: 0,
            curmbsIndex: 1, // 
            navLeft: config.navLeft,
            needToLogin: true
          }
        },
        { // -
          path: 'developerMan',
          name: 'developerMan',
          component: supplyMan,
          meta: {
            title: '',
            userType: constans['SERVICE_USER'],
            index: 1,
            curmbsIndex: 1, // 
            navLeft: config.navLeft,
            needToLogin: true
          }
        }]
    },
    { // -
      path: '/openApply',
      redirect: {
        name: 'openApplyMan'
      },
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: wrap,
        footer: footer
      },
      children: [{
        path: 'openApplyList',
        name: 'openApplyMan',
        component: openApplyMan,
        meta: {
          title: '',
          opera: true,
          curmbsIndex: 1, // 
          pName: 'open',
          navLeft: config.navLeft,
          needToLogin: true
        }
      }, { // -
        path: 'openApplyDetail',
        name: 'openAppDetail',
        component: openApplyDetail,
        meta: {
          title: 'API',
          curmbsIndex: 2, // 
          pName: 'open',
          opera: true,
          navLeft: config.navLeft,
          needToLogin: true
        }
      }, { // -api
        path: 'apiDetail',
        name: 'openApiDetail',
        component: apiDetail,
        meta: {
          title: 'API',
          opera: true,
          curmbsIndex: 3, // 
          pName: 'open',
          navLeft: config.navLeft,
          needToLogin: true
        }
      }, { // -
        path: 'guideDetail',
        name: 'openGuideDetail',
        component: guideDetail,
        meta: {
          title: '',
          pName: 'open',
          navLeft: config.navLeft,
          needToLogin: true,
          curmbsIndex: 3 // 
        }
      }]
    },
    { // -
      path: '/diyApply',
      redirect: {
        name: 'diyApplyMan'
      },
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: wrap,
        footer: footer
      },
      children: [{
        path: 'diyApplyMan',
        name: 'diyApplyMan',
        component: diyApplyMan,
        meta: {
          title: '',
          curmbsIndex: 1, // 
          navLeft: config.navLeft,
          needToLogin: true
        }
      }, { // -
        path: 'diyApplyDetail',
        name: 'diyApplyDetail',
        component: diyApplyDetail,
        meta: {
          title: '',
          curmbsIndex: 2, // 
          pName: 'diy',
          navLeft: config.navLeft,
          needToLogin: true
        }
      }, {
        path: 'openApplyDetail',
        name: 'diyAppDetail',
        component: openApplyDetail,
        meta: {
          title: 'API',
          curmbsIndex: 3, // 
          pName: 'diy',
          navLeft: config.navLeft,
          needToLogin: true
        }
      }, { // -api
        path: 'apiDetail',
        name: 'diyApiDetail',
        component: apiDetail,
        meta: {
          title: 'API',
          curmbsIndex: 4, // 
          pName: 'diy',
          navLeft: config.navLeft,
          needToLogin: true
        }
      }, { // -
        path: 'guideDetail',
        name: 'diyGuideDetail',
        component: guideDetail,
        meta: {
          title: '',
          curmbsIndex: 4, // 
          pName: 'diy',
          navLeft: config.navLeft,
          needToLogin: true
        }
      }]
    },
    { // -
      path: '/gwMan',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: wrap,
        footer: footer
      },
      redirect: {
        name: 'gwList'
      },
      children: [{
        path: 'gwList',
        name: 'gwList',
        component: gwList,
        meta: {
          title: '',
          curmbsIndex: 1, // 
          navLeft: config.navLeft,
          needToLogin: true
        }
      }, { // -
        path: 'nodeList',
        name: 'nodeList',
        component: nodeList,
        meta: {
          title: '',
          curmbsIndex: 2, // 
          navLeft: config.navLeft,
          needToLogin: true
        }
      }]
    }]
})

import * as types from '@/store/mutation-types.js'
import config from '@/assets/js/config.js'
// 
router.beforeEach((to, from, next) => {
  router.app.curmbsData = to.meta.curmbsData
  let userInfo = localStorage.getItem('adminSession')
  let oUserInfo
  if (userInfo) {
    userInfo = JSON.parse(userInfo)
    let __time = userInfo.time * 1
    oUserInfo = userInfo.userInfo
    let sNowTime = new Date().getTime() * 1
    let expiredTime = config.expiredTime // 
    oUserInfo = sNowTime - __time < expiredTime ? oUserInfo : null
    router.app.$options.store.commit(types.UPDATEUSERINFO, oUserInfo)
  }
  if (!router.app.userInfo && !userInfo && !oUserInfo && to.meta.needToLogin) { // 
    return next({name: 'index'})
  }
  next()
})
export default router



